<template>
    <v-row class="ma-0 idle_background" align="center" justify="center">
        <div class="close_app_btn_area">
            <v-btn @click="programCloseDialog = true" text class="close_app_btn">
                close
            </v-btn>
        </div>
        <v-col align="center">
            <video width="100%" autoplay loop :muted="true" @click="onClickScreen()">
                <source :src="intro" type="video/mp4"/>
            </video>
            <common-input-number
                :dialog="inputNumberOn"
                v-on:close-dialog="CloseDialog()"
            />
            <common-confirm-dialog
                :dialog="programCloseDialog"
                :dialogItems="programCloseDialogItems"
                v-on:close-cancel="CloseDialog()"
                v-on:close-allow="CloseApp()"
            />
        </v-col>
    </v-row>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'

export default {
    data:()=>({
        inputNumberOn: false,
        programCloseDialog: false,
        programCloseDialogItems:{
            title: '프로그램 종료',
            text: 'SMART MIRROR를 종료하시겠습니까?',
            close: '취소',
            confirm: '확인'
        },
        selectItem: 0
    }),
    computed: {
        ...mapGetters([
            'GET_APP_AUTH_MEMBER_INFO',
        ]),
        intro(){
            let video = require(`@/assets/videos/smart_intro.mp4`)
            return video
        }
    },
    created() {
        this.$eventBus.$on('E_CardReader', this.initializeNFCReader)
    },
    mounted(){
    },
    beforeDestroy () {
        this.$eventBus.$off('E_CardReader')
    },
    methods: {      
        ...mapActions([
            'ACT_APP_API_AUTH_MEMBER_BY_CARD',
            'ACT_APP_API_GET_MEMBER_INFO',
        ]),
        async onClickScreen() {
            if(process.env.VUE_APP_USE_NUMBER === 'true'){
                // this.inputNumberOn = true    // 번호로 로그인하는 popup추가 api구현안됨
            }
            // 임시 click 입장
            const params = {
                UID : 'C78EA0AC',
                machineSN : process.env.VUE_APP_MACHINE_SN,
                macAddr : '',
                ipAddr : ''
            }
            await this.ACT_APP_API_AUTH_MEMBER_BY_CARD(params)   // card login
            if(this.GET_APP_AUTH_MEMBER_INFO === null) {
                this.$toastr.error  ( 
                    '로그인 오류', 
                    '로그인 멤버 정보가 없습니다.' 
                    ,{
                        position: 'toast-top-center',
                        clickClose: true
                    }
                )
                return
            }
            await this.ACT_APP_API_GET_MEMBER_INFO()             // get member info
            .then(()=>{
                this.$router.push('/user/welcome')
            })
        },
        initializeNFCReader(uid){
            this.onReceivedNFC(uid)
        },
        async onReceivedNFC (uid) {
            const params = {
                UID: uid,
                machineSN : process.env.VUE_APP_MACHINE_SN,
                macAddr : '',
                ipAddr : ''
            }
            await this.ACT_APP_API_AUTH_MEMBER_BY_CARD(params)   // card login
            if(this.GET_APP_AUTH_MEMBER_INFO === null) {
                this.$toastr.error  ( 
                    '로그인 오류', 
                    '로그인 멤버 정보가 없습니다.' 
                    ,{
                        position: 'toast-top-center',
                        clickClose: true
                    }
                )
                return
            }            
            await this.ACT_APP_API_GET_MEMBER_INFO()             // get member info
            .then(()=>{
                this.$router.push('/user/welcome')
            })
        },
        CloseDialog(){
            this.inputNumberOn = false
            this.programCloseDialog = false
        },
        CloseApp(){
        }
    },
}
</script>

<style scoped>
    .idle_background {
        background-color: black;
        color: white;
    }
</style>